import React from 'react';
import styled from 'styled-components';

/* ========== Utilities ========== */
import { Flex } from 'components/utilities/Flex';
import { Stack } from 'components/utilities/Stack';
import { ImgContainer } from 'components/utilities/ImgContainer';
import IsVerified from 'components/utilities/isVerified';

const EventCard = ({ Event, click }) => {
  return (
    <StyledEventCard onClick={click}>
      <Flex gap={'1rem'} alignItem="center">
        <ImgContainer height={'1.5rem'} width={'1.5rem'} radius={'50%'}>
          <img src={Event.creator.img} alt={Event.creator.name} />
        </ImgContainer>
        <Stack className={'greyText'}>
          <Flex gap={'.5rem'}>
            <div style={{ color: 'white' }}>{Event.creator.name}</div>
            <div style={{ color: '#848486' }}>
              {Event.creator?.isVerified && <IsVerified />}
            </div>
          </Flex>
          {Event.time}
        </Stack>
        <p style={{ marginLeft: 'auto', fontSize: '1.25rem', color: '' }}>
          {Event.price}
        </p>
      </Flex>
      <Stack>
        <div className="scheduled-preview">
          <span>{Event.date.month}</span>
          <span>{Event.date.day}</span>
        </div>
        <ImgContainer
          height={'180px'}
          border={'4px solid #141518'}
          oFit={'cover'}
          radius={'1rem'}
        >
          <img src={Event.img} alt="" />
        </ImgContainer>
        <span style={{ textAlign: 'center', marginTop: '.25rem' }}>
          {Event.name}
        </span>
      </Stack>
    </StyledEventCard>
  );
};

export default EventCard;

/* ========== Styling ========== */
const StyledEventCard = styled.div`
  line-height: 1;
  font-family: 'Brown';
  font-weight: lighter;
  padding: 1rem;
  transition: transform 300ms ease-in-out;

  &:hover {
    cursor: pointer;
    transform: translateY(0.35rem);

    .event__picture {
      img {
        transform: scale(1.1);
      }
    }
  }

  .greyText {
    color: gray;
  }

  .scheduled-preview {
    position: absolute;
    top: 1rem;
    left: 1rem;

    height: max-content;
    width: max-content;

    padding: 0.25rem 0.45rem;
    z-index: 20;

    display: grid;
    place-items: center;
    gap: 0.05rem;
    border: 1px solid ${({ theme }) => theme.colors.purple};
    line-height: 1;

    color: ${({ theme }) => theme.colors.lightBlack};
    background-color: ${({ theme }) => theme.colors.white};
    border-radius: 0.35rem;

    font-size: 0.85rem;

    span:first-child {
      color: ${({ theme }) => theme.colors.red};
    }
  }
`;
