import styled from 'styled-components';

export const Tabs = styled.div`
  display: flex;
  border-bottom: 2px solid ${({ theme }) => theme.colors.gray};

  > * {
    width: max-content;
    padding: ${({ padding }) => padding};
    line-height: 1.1;

    display: grid;
    place-items: center;
    text-align: center;

    &.active,
    &:hover {
      border-bottom: 2px solid ${({ theme }) => theme.colors.purple};
    }

    &:hover {
      cursor: pointer;
    }
  }
`;

Tabs.defaultProps = {
  padding: '10px 20px'
};
