import Button from 'components/buttons/Button';
import { Container } from 'components/utilities/Container';
import InputField from 'components/utilities/InputField';
import React from 'react';

const CreateNewEvent = () => {
  return (
    <Container className="hide-mobile">
      <h3>Create an event</h3>
      <div className="create-event grid-form">
        <div className="create-event__image">
          <p>Click to upload event banner</p>
          <input type="file" name="upload-event-image" id="uploadEventImage" />
        </div>
        <InputField>
          <label htmlFor="Event Name">Event name</label>
          <input type="text" />
        </InputField>
        <InputField>
          <label htmlFor="Event Name">Event Description</label>
          <textarea type="text" />
        </InputField>

        <div className="flex-group">
          <InputField>
            <label htmlFor="Event Name">Event Date</label>
            <input type="date" />
          </InputField>
          <InputField>
            <label htmlFor="Event Name">Event Time</label>
            <input type="time" />
          </InputField>
        </div>
        <div className="flex-group">
          <InputField>
            <label htmlFor="Event Name">Event Type</label>
            <select name="" id="">
              <option value="">Select category</option>
            </select>
          </InputField>
          <InputField>
            <label htmlFor="Event Name">Set Event Category</label>
            <select name="" id="">
              <option value="">Select category</option>
            </select>
          </InputField>
        </div>
      </div>

      <Button padding={'1.5rem'} width={'90%'}>
        Create Event
      </Button>
    </Container>
  );
};

export default CreateNewEvent;
