import React from 'react';
import styled from 'styled-components';

const IsVerified = () => {
  return <StyledIsVerified></StyledIsVerified>;
};

export const StyledIsVerified = styled.div`
  height: 1rem;
  width: 1rem;
  border-radius: 50%;
  background-color: purple;
  display: grid;
  place-items: center;
`;
export default IsVerified;
