import React, { useContext, useState } from 'react';
import styled from 'styled-components';
import { Link } from 'react-router-dom';

/* ========== Icons ========== */
import { ReactComponent as SearchIcon } from 'assets/icons/svg/search_icon.svg';
import { ReactComponent as WalletIcon } from 'assets/icons/svg/wallet_icon.svg';
import { ReactComponent as EventIcon } from 'assets/icons/svg/Plan_Event.svg';
import { ReactComponent as NotificationIcon } from 'assets/icons/svg/notification_icon.svg';
import { ReactComponent as TicketIcon } from 'assets/icons/svg/ticket_icon.svg';

/* ========== Import Context ========== */
import LayoutContext from 'contexts/LayoutContext';
import AuthContext from 'providers/AuthProvider';
import { LogoContainer } from 'components/utilities/Logo';
import { ImgContainer } from 'components/utilities/ImgContainer';
import { IconWrapper } from 'components/utilities/IconWrapper';
import { Flex } from 'components/utilities/Flex';
import ManageEvents from 'components/sidepane/ManageEvents';
import Notifications from 'components/sidepane/Notifications';
import Wallet from 'components/sidepane/Wallet/Index';
import DataContext from 'contexts/DataContext';

/* ========== Styles ========== */
const StyledHeader = styled.header`
  height: 5.5rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-right: 2rem;

  padding-left: 1rem;
  background-color: ${({ theme }) => theme.colors.lightBlack};
  color: ${({ theme }) => theme.colors.darkGray};
  gap: 2rem;

  position: relative;
  z-index: 1000;

  svg {
    stroke: ${({ theme }) => theme.colors.white};
  }

  .search {
    display: flex;
    flex-grow: 1;
    justify-content: flex-end;
    align-items: center;
    gap: 1rem;

    input {
      width: 60%;
      border-radius: 0.65rem;
      background-color: ${({ theme }) => theme.colors.darkBlack};
      border: none;
      padding: 1rem;
      font-family: 'Brown';
      font-size: 1rem;
      color: ${({ theme }) => theme.colors.darkWhite};

      &::placeholder {
        font-size: 1.1rem;
      }
    }
  }

  .searchButton {
    background: ${({ theme }) => theme.colors.purple};
    padding: 0.65rem 1.5rem;
    border-radius: 0.65rem;
    display: grid;
    place-items: center;

    svg {
      height: 1.5rem;
    }
  }

  .notification-count {
    position: absolute;

    top: -10px;
    right: -5px;
    background-color: red;

    border-radius: 50%;
    height: 1rem;
    width: 1rem;
    font-size: 0.5rem;
    font-weight: bold;
    line-height: 0;
    color: white;

    display: grid;
    place-items: center;
  }
`;

const HeaderNav = styled.nav`
  display: none;

  @media (min-width: ${({ theme }) => theme.screens.minSize.ipad}) {
    display: flex;
    align-items: center;
    justify-content: flex-end;
    gap: 1.5rem;
    width: 100%;
  }
`;

const Header = () => {
  const { user } = useContext(DataContext);

  const newUser = user;

  const { setPreview } = useContext(LayoutContext);
  const [notificationCount, setNotificationCount] = useState(
    newUser.notification.count
  );
  const { auth } = useContext(AuthContext);

  /* ========== New Component ========== */
  return (
    <StyledHeader>
      <Link to={'/'}>
        <LogoContainer>
          <ImgContainer height={'3rem'} width={'3rem'}>
            <img
              src={'../../assets/img/ravenrevampedlogo.png'}
              alt="Raven Livestream logo"
            />
          </ImgContainer>
          Raven Livestream
        </LogoContainer>
      </Link>
      {auth ? (
        <HeaderNav>
          <div className="search">
            <input
              type="text"
              placeholder="Search for events, people and ticket"
            />
            <Link to={'/search'}>
              <div className="searchButton">
                <SearchIcon />
              </div>
            </Link>
          </div>

          <Flex gap="1.5rem" width="max-content">
            <IconWrapper onClick={() => setPreview(<Wallet />, true, true)}>
              <WalletIcon />
            </IconWrapper>

            <IconWrapper
              onClick={() => setPreview(<ManageEvents />, true, true)}
            >
              <EventIcon />
            </IconWrapper>

            <IconWrapper
              onClick={() => {
                newUser.readNotification();
                setNotificationCount(newUser.notification.count);
                setPreview(<Notifications />, true, true);
              }}
            >
              <NotificationIcon />
              {notificationCount >= 1 ? (
                <div className="notification-count">{notificationCount}</div>
              ) : (
                ''
              )}
            </IconWrapper>

            <Link to={'/tickets'}>
              <IconWrapper>
                <TicketIcon />
              </IconWrapper>
            </Link>

            <Link to={'/profile'}>
              <IconWrapper>
                <ImgContainer height={'100%'} oFit={'cover'} radius={'50%'}>
                  <img src={newUser.image} alt="" />
                </ImgContainer>
              </IconWrapper>
            </Link>
          </Flex>
        </HeaderNav>
      ) : (
        ''
      )}
    </StyledHeader>
  );
};

export default Header;
