import React, { useContext } from 'react';

/* ========== Layouts ========== */
import { Flex } from 'components/utilities/Flex';
import { Stack } from 'components/utilities/Stack';
import { Container } from 'components/utilities/Container';

/* ========== Components ========== */
import Button from 'components/buttons/Button';
import { StyledTicketPreviewCard } from './style/TicketPreview.style';
import DataContext from 'contexts/DataContext';
import { ImgContainer } from 'components/utilities/ImgContainer';
import LayoutContext from 'contexts/LayoutContext';
import EventPreview from '../EventPreview/[id]';

export default function TicketsPreview({ Ticket }) {
  const { Events } = useContext(DataContext);
  const { setPreview } = useContext(LayoutContext);

  let id = localStorage.getItem('TicketId');

  Ticket = Events.find((Ticket) => Number(Ticket.id) === Number(id));

  const setEventPreview = (key) => {
    localStorage.setItem('EventId', key);
    setPreview(<EventPreview />, true, true);
  };

  /* ========== New Component ========== */
  return (
    <>
      <Container
        onClick={() => setEventPreview(Ticket.id)}
        padding="4rem 2rem 2rem"
        style={{ fontFamily: 'Brown' }}
      >
        <p
          style={{
            textAlign: 'center',
            color: 'gray',
            paddingBottom: '1rem'
          }}
        >
          click ticket to view event details
        </p>
        <StyledTicketPreviewCard style={{ paddingBottom: '0' }}>
          <p
            style={{
              fontSize: '1.35rem',
              lineHeight: 1.2,
              paddingBottom: '20px'
            }}
          >
            {Ticket.name}
          </p>
          <Flex
            justifyContent={'space-between'}
            padding={'1rem 0'}
            style={{ borderTop: '1px solid gray', color: 'gray' }}
          >
            <p>
              {Ticket.date.day} {Ticket.date.month}
            </p>
            <p>{Ticket.time}</p>
          </Flex>
          <Stack
            gap={'.25rem'}
            padding={'1rem 0'}
            style={{ borderTop: '1px solid gray' }}
          >
            <p>2 Tickets Avaliable</p>
            {Ticket.price}
          </Stack>

          <Flex
            justifyContent={'center'}
            gap={'1rem'}
            alignItem={'center'}
            padding={'1rem 0'}
            width={'max-content'}
            style={{
              borderTop: '1px solid gray'
            }}
          >
            <ImgContainer height={'2rem'} width={'2rem'} radius={'50%'}>
              <img src={Ticket.creator.img} alt="" />
            </ImgContainer>

            <Stack gap={'.25rem'} style={{ textAlign: 'left' }}>
              <p>{Ticket.creator.name}</p>
              <p style={{ color: 'gray' }}>{Ticket.creator?.role}</p>
            </Stack>
          </Flex>
        </StyledTicketPreviewCard>
      </Container>

      <Stack gap={'1rem'}>
        <Button padding={'1.5rem'} width={'60%'}>
          Share Ticket
        </Button>
        <Button padding={'1.5rem'} width={'60%'} outline={true}>
          Cancel Ticket
        </Button>
      </Stack>
    </>
  );
}
